import {getIngredientsRequest} from "../api";
import {number} from "prop-types";
import {TIngredient, TUnionActions} from "./dnd";
import {ThunkFunc} from "../hooks/hooks";

export const GET_INGREDIENTS_REQUEST = 'GET_INGREDIENTS_REQUEST';
export const GET_INGREDIENTS_SUCCESS = 'GET_INGREDIENTS_SUCCESS';
export const GET_INGREDIENTS_FAILED = 'GET_INGREDIENTS_FAILED';

export interface IGET_INGREDIENTS_REQUEST_ACTION {
    readonly type: typeof GET_INGREDIENTS_REQUEST;
    readonly payload: boolean;
}

export interface IGET_INGREDIENTS_SUCCESS_ACTION {
    readonly type: typeof GET_INGREDIENTS_SUCCESS;
    readonly payload: boolean;
    ingredients: Array<TIngredient>;
}

export interface IGET_INGREDIENTS_FAILED_ACTION {
    readonly type: typeof GET_INGREDIENTS_FAILED;
    readonly payload: boolean;
}

export const getIngredients: ThunkFunc = () => {
    //      redux-thunk - dispatch
    return function(dispatch: ThunkFunc) {
        //      ,     
        //  ,       
        //     
        dispatch({
            type: GET_INGREDIENTS_REQUEST
        })
        //    

            getIngredientsRequest()
            .then(res => {
                //       
                //      
                dispatch({
                    type: GET_INGREDIENTS_SUCCESS,
                    ingredients: res.data
                })
        }).catch( err => {
            //     ,    
            dispatch({
                type: GET_INGREDIENTS_FAILED
            })
        })
    }
}

export type TUnionIngredientsActions =
    | IGET_INGREDIENTS_REQUEST_ACTION
    | IGET_INGREDIENTS_SUCCESS_ACTION
    | IGET_INGREDIENTS_FAILED_ACTION